import React, { useMemo } from 'react';

export const DAY_MAP = ['', '', '','','','', '']

export const useCurrentDate = () => {
  return useMemo(() => {
    const d = new Date();
    const day = d.getDay();
    const hours = d.getHours();
    return {
      day,
      hours,
      dayText: DAY_MAP[day]
    }
  }, [])
};

